<template>
    <div style="border:1px solid #e0e0e0;width:30%;border-radius:5px;background:#ffffff">
        <h1></h1>
        <hr style="width:90%">
        <form
            id = "outer"
            style="text-align:center;padding-left:10%;padding-right:10%;padding-top:5px;padding-bottom:5px"
            novalidate="novalidate"
        >
            <!---->
            <div style="text-align:left;width:100%;padding:1ex">
                <label for="Username" style="text-align:left;font-size:120%"><span style="color:red">*</span></label><br>
                <el-input 
                    type="input" 
                    name="Username" 
                    id="Username" 
                    v-model="Username" 
                    placeHolder="" 
                    style="width:100%;font-size:120%;margin-top:0.5ex"
                    @blur="validateUsername()"
                />
                <br>
            </div>
            <!---->
            <div style="text-align:left;width:100%;padding:1ex">
                <label for="mail" style="margin-top:1ex;text-align:left;font-size:120%"><span style="color:red">*</span></label><br>
                <el-input 
                    type="email" 
                    name="Email" 
                    placeholder="" 
                    id="mail" 
                    v-model="email" 
                    style="width:75%;font-size:120%;margin-top:0.5ex"
                />
                <div style="display:inline-block;text-align:right;width:24%">
                    <el-button type="primary" plain class="button" size="medium" style="width:100%;margin-right:0px;font-size:120%" @click="sendVerify()">
                        
                    </el-button>
                </div>
                <br>
            </div>
            <!---->
            <div style="text-align:left;width:100%;padding:1ex" v-if="emailSent">
                <label for="captcha" style="text-align:left;font-size:120%"><span style="color:red">*</span></label><br>
                <el-input 
                    type="input" 
                    name="Captcha" 
                    id="captcha" 
                    v-model="captcha" 
                    style="width:100%;font-size:120%;margin-top:0.5ex"
                /><br>
            </div>
            <!---->
            <div style="text-align:left;width:100%;padding:1ex;">
                <label for="password" style="text-align:left;font-size:120%"><span style="color:red">*</span></label><br>
                <el-input 
                    type="password" 
                    name="Password" 
                    placeholder="" 
                    id="password" 
                    v-model="password" 
                    style="width:100%;font-size:120%;margin-top:0.5ex"
                    show-password
                />
                <br>
            </div>
            <!---->
            <div style="text-align:left;width:100%;padding:1ex">
                <label for="password-again" style="text-align:left;font-size:120%"><span style="color:red">*</span></label><br>
                <el-input 
                    type="password" 
                    placeholder="" 
                    id="password-again" 
                    v-model="again" 
                    style="width:100%;font-size:120%;margin-top:0.5ex"

                /><br>
            </div>
            <el-button plain type="primary" class="button" value="" style="margin-top:1ex;width:30%;margin-bottom:1ex" @click="FormSubmit()"></el-button>
        </form>
    </div>
</template>
<script>
export default {
    data(){
        return{
            Username:"",
            email:"",
            password:"",
            again:"",
            captcha:"",
            emailSent:false,
            UsernameOK:false,
            emailOK:false,
            passwordOK:false
        }
    },
    methods:{
        /* */
        async FormSubmit(){
            console.log("");
            let form = new FormData();
            var isPasswordOk = await this.validatePassword();
            if(!isPasswordOk){
                console.log("");
                return;
            }
            var isEmailOk = await this.validateEmailAddr();
            if(!isEmailOk){
                console.log("");
                return;
            }
            var isUsernameOk = await this.validateUsername();
            if(!isUsernameOk){
                console.log("");
                return;
            }
            if(this.captcha==""){
                this.$message({
                    message:"",
                    type:"error",
                    duration:1000,
                    showClose:true
                });
                console.log("");
                return;
            }
            form.append("Captcha",this.captcha);
            form.append("Username",this.Username);
            form.append("Password",this.$md5(this.password));
            form.append("Email",this.email);
            this.$axios.post("/api/register/",form)
            .then(response=>{
                if(response.data.Status){
                    this.$message({
                        message:"",
                        type:"success",
                        duration:1000,
                        showClose:true
                    })
                    this.$router.push("/");
                }
                else{
                    this.$message({
                        message:"",
                        type:"error",
                        duration:1000,
                        showClose:true
                    })
                }
            }).catch(error=>{
                this.$message({
                        message:"",
                        type:"error",
                        duration:1000,
                        showClose:true
                    })
            })
        },
        /* */
        async sendVerify(){
            var isEmailOk = await this.validateEmailAddr();
            if(!isEmailOk){

            }
            else{
                this.emailSent=true;
                console.log("")
                let form = new FormData();
                form.append("Email",this.email);
                this.$axios.post("/api/captcha/",form)
                .then(response=>{
                    if(response.data.Status){
                        this.$message({
                            message:"10",
                            type:"success",
                            duration:1000,
                            showClose:true
                        })
                    }
                    else{
                        this.$message({
                            message:"",
                            type:"error",
                            duration:1000,
                            showClose:true
                        })
                    }
                }).catch(error=>{
                    this.$message({
                        message:"",
                        type:"error",
                        duration:1000,
                        showClose:true
                    })
                })
            }
        },
        /* */
        judgeEmailAddr(email){
            var emreg=/^\w{3,}(\.\w+)*@[A-z0-9]+(\.[A-z]{2,5}){1,2}$/;
            return emreg.test(email);
        },
        /* */
        async validateEmailAddr(){
            if(!this.judgeEmailAddr(this.email)){//
                console.log("");
                this.$message({
                    message:"",
                    type:"error",
                    duration:1000,
                    showClose:true
                })
                return false;
            }
            else{
                let form = new FormData();
                form.append("Email",this.email);
                var result;
                await this.$axios.post("/api/checkEmail/",form)
                .then(response=>{
                    if(response.data.Used){
                        this.$message({
                            message:"",
                            type:"error",
                            duration:1000,
                            showClose:true
                        })
                        result = false;
                    }
                    else result = true;
                })
                .catch(error=>{
                    this.$message({
                        message:"",
                        type:"error",
                        duration:1000,
                        showClose:true
                    })
                    result = false;
                })
                return result;
            }
        },
        /* */
        async validateUsername(){
            this.UsernameOK=false;
            if(this.Username==""){
                this.$message({
                    message:"",
                    type:"error",
                    duration:1000,
                    showClose:true
                })
                return false;
            }
            else{
                let form = new FormData();
                form.append("Username",this.Username);
                var result;
                await this.$axios.post("/api/checkUsername/",form)
                .then(response=>{
                    if(response.data.Used){
                        console.log(":"+response.data.Used);
                        this.$message({
                            message:"",
                            type:"error",
                            duration:1000,
                            showClose:true
                        })
                        console.log("1");
                        result = false;
                    }
                    else{
                        this.UsernameOK=true;
                        console.log(":"+response.data.Used);
                        console.log("2");
                        result = true;
                    }
                })
                .catch(error=>{
                    console.log("3");
                    result = false;
                })
                console.log(""+result);
                return result;
            }
        },
        /* */
        async validatePassword(){
            console.log("");
            var i;
            if(this.password.length!=this.again.length){
                this.$message({
                    message:"",
                    type:"error",
                    duration:1000,
                    showClose:true
                })
                return false;
            }
            var hasCapital = false;
            var hasNumber = false;
            var hasNormal = false;
            for(i=0;i<this.password.length;i++){
                if(this.password.charAt(i)!=this.again.charAt(i)){
                    this.$message({
                        message:"",
                        type:"error",
                        duration:1000,
                        showClose:true
                    });
                    return false;
                }
                if(this.password.charAt(i)>='a'&&this.password.charAt(i)<='z'){
                    console.log("");
                    hasNormal=true;
                }
                if(this.password.charAt(i)>='0'&&this.password.charAt(i)<='9'){
                    console.log("");
                    hasNumber=true;
                }
                if(this.password.charAt(i)>='A'&&this.password.charAt(i)<='Z'){
                    console.log("");
                    hasCapital=true;
                }
            }
            if(!(hasNormal&&hasNumber&&hasCapital)){
                if(!hasNumber){
                    this.$message({
                        message:"",
                        type:"error",
                        duration:1000,
                        showClose:true
                    });
                }
                else if(!hasCapital){
                    this.$message({
                        message:"",
                        type:"error",
                        duration:1000,
                        showClose:true
                    });
                }
                else{
                    this.$message({
                        message:"",
                        type:"error",
                        duration:1000,
                        showClose:true
                    });
                }
                return false;
            }
            return true;
        }
    }
}
</script>
<style scoped>

</style>




