import Vue from 'vue'
import Vuex from 'vuex'

//Vuex
Vue.use(Vuex)

//VueX
const store = new Vuex.Store({
    state:{
        //
        userProfile: {
            name: window.sessionStorage.getItem("Username"),//
            UID: window.sessionStorage.getItem("UID"), // ID
            avatarUrl:window.sessionStorage.getItem("AvatarUrl")
        },
        isRegister: window.sessionStorage.getItem("isRegister"), //
    },
    mutations:{
        //es6edit:funcion(){...}
        changeUserName(state,payload){
            state.userProfile.name=payload;
            window.sessionStorage.setItem("Username",payload);
        },
        changeUserState(state,payload){
            state.isRegister = payload;
            window.sessionStorage.setItem("isRegister",payload);
        },
        changeUID(state,payload){
            state.userProfile.UID=payload;
            window.sessionStorage.setItem("UID",payload);
        },
        changeAvatarUrl(state,payload){
            state.userProfile.avatarUrl = payload;
            window.sessionStorage.setItem("AvatarUrl",payload);
        }
    },
    getters:{
        nameInfo(state){
            return ":"+state.name
        },
        fullInfo(state,getters){
            return getters.nameInfo+':'+state.age
        }  
    }
})

export default store


