<template>
    <div style="border:1px solid #e0e0e0;width:30%;border-radius:5px;background:#ffffff">
        <h1></h1>
        <hr style="width:90%">
        <form
            id = "outer"
            style="text-align:center;padding-left:10%;padding-right:10%;padding-top:5px;padding-bottom:5px"
            @submit.prevent="FormSubmit()"
        >
            <!---->
            <div style="text-align:left;width:100%;padding:1ex">
                <label for="Username" style="text-align:left;font-size:120%"><span style="color:red">*</span></label><br>
                <el-input 
                    type="input" 
                    required="required" 
                    name="Username" 
                    id="Username" 
                    v-model="email" 
                    placeHolder="" 
                    style="width:100%;font-size:120%;margin-top:0.5ex"
                />
                <br>
            </div>
            <!---->
            <div style="text-align:left;width:100%;padding:1ex">
                <label for="password" style="text-align:left;font-size:120%"><span style="color:red">*</span></label><br>
                <el-input 
                    type="password" 
                    required="required" 
                    name="Password" 
                    placeholder="" 
                    id="password" 
                    v-model="password" 
                    style="width:100%;font-size:120%;margin-top:0.5ex"
                    show-password
                /><br>
            </div>
            <el-button plain type="primary" class="button" style="margin-top:1.5ex;width:40%;margin-bottom:1.5ex" @click="FormSubmit()"></el-button>
        </form>
    </div>
</template>
<script>
export default {
    data(){
        return{
            email:"",
            password:""
        }
    },
    methods:{
        /* */
        FormSubmit(){
            if(this.Email==""){
                this.$message({
                    message:"/",
                    duration:1000,
                    showClose:true,
                    type:"error"
                })
                return;
            }
            else if(this.password==""){
                this.$message({
                    message:"",
                    duration:1000,
                    showClose:true,
                    type:"error"
                })
            }
            else{
                let form = new FormData();
                form.append("Email", this.email);
                form.append("Password", this.$md5(this.password));
                this.$axios.post("/api/login/",form)
                .then(response=>{
                    if(response.data.Status){//
                        this.$message({
                            message:"",
                            type:"success",
                            duration:1000,
                            showClose:true
                        })
                        this.$store.commit('changeUID',response.data.UserId);
                        console.log("UID changed!");
                        this.$store.commit('changeUserName',response.data.Username);
                        console.log("Username changed!");
                        if(response.data.AvatarUrl!=""){
                            this.$store.commit('changeAvatarUrl',"http://119.45.200.4/"+response.data.AvatarUrl);
                        }else{
                            this.$store.commit('changeAvatarUrl',response.data.AvatarUrl);
                            console.log("AvatarUrl")
                        }
                        console.log("AvatarUrl changed!");
                        this.$store.commit('changeUserState',true);
                        console.log("User State changed!");
                        console.log("ToRead"+sessionStorage.getItem("ToRead"))
                        if(window.sessionStorage.getItem("ToRead")==null||window.sessionStorage.getItem("ToRead")=="null"){
                            this.$router.push("/craftTable/"+this.$store.state.userProfile.UID)
                            sessionStorage.setItem("ToRead",null);
                            console.log(sessionStorage.getItem("ToRead"));
                        }
                        else{
                            this.$router.push("/sharepage/"+this.$store.state.userProfile.UID+"/"+sessionStorage.getItem("ToRead"))
                            sessionStorage.setItem("ToRead",null);
                            console.log(sessionStorage.getItem("ToRead"))
                        }
                    }
                    else{//
                        this.$message({
                            message:"",
                            type:"error",
                            duration:1000,
                            showClose:true
                        })
                    }
                })
                .catch(error=>{
                    this.$message({
                        message:"",
                        type:"error",
                        duration:1000,
                        showClose:true
                    })
                })
            }
        },
    }
}
</script>
<style scoped>

</style>


