<template>
    <!---->
    <div>
        <!---->
        <div style="display:inline-block;width:75%;vertical-align:top">
            <!---->
            <input
                type="text"
                style="width:95%;
                    border:0px;
                    border-bottom:1px solid #aaaaaa;
                    font-size:200%;
                    margin-top:5px;
                    text-align:center;
                    padding:5px;
                    "
                placeholder=""
                v-model="articleInfo.title"
                :disabled="!this.Editable"
            />
            <!---->
            <mavon-editor
                style="width:99%;margin-top:10px;box-shadow:0px 0px 0px #fff;height:90ex"
                v-model="articleInfo.content"
                :subfield="this.Editable"
                :defaultOpen="'preview'"
                ref="editor"
                @imgAdd="$imgAdd"
                :toolbarsFlag="this.Editable"
            />
        </div>
        <!---->
        <div style="display:inline-block;text-align:center;width:20%;vertical-align:top">
            <div style="width:100%;margin-top:2ex">
                <!---->
                <el-button plain style="width:80%" @click="saveAsMine('doc')" :disabled="!this.Editable">
                    
                </el-button>
                <br>
            </div>
            <div v-if="aid!=-1" style="width:100%;margin-top:2ex">
                <!---->
                <div>
                    <h3 style="padding:1ex;margin:auto auto auto auto;border-bottom:1px solid #e8e8e8"></h3>
                    <div class="authOption">
                        <p class="authTitle"><i class="el-icon-position"></i>&nbsp;</p>
                        <p class="switches">{{haveviews}}</p>
                    </div>
                    <div class="authOption">
                        <p class="authTitle"><i class="el-icon-s-order"></i>&nbsp;</p>
                        <p class="switches">{{havemodifys}}</p>
                    </div>
                    <div class="authOption">
                        <p class="authTitle"><i class="el-icon-chat-line-round"></i>&nbsp;</p>
                        <p class="switches">{{havecomments}}</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import viewNumSider from "@/components/viewNumSider/viewNumSider.vue";
import WeightList from "@/components/WeightList/WeightList.vue"
import WeightList2 from "@/components/WeightList2/WeightList2.vue"
export default {
    components:{
        WeightList,
        WeightList2,
        viewNumSider
    },
    props:{
        Editable:Boolean,//
        MyLevel:Number,//
        isRead:Boolean
    },
    data(){
        return{
            haveviews:0,
            havemodifys:0,
            havecomments:0,
            md5:String,
            aid:Number,//id
            uid:Number,//id
            tid:Number,//id
            mid:Number,//id
            articleInfo:{//
                content:"",//
                title:"",//
            },
            Tags:[],
            inputVisible:false,
            inputValue:{
                name:'',
                type:''
            },
            checkVisible:1,
            noTag:true
        }
    },
    /* */
    created(){
        this.tid=parseInt(this.$route.params.TID==undefined?"-1":this.$route.params.TID);
        //this.aid=parseInt(this.$route.params.AID);
        this.md5=this.$route.params.AID;
        this.uid=parseInt(this.$route.params.UID);
        this.mid=parseInt(this.$route.params.MID);
        //this.MD5getAID();
        /* */
        if((!isNaN(this.uid))&&this.uid!=null){
            this.MD5getAID()
        }
    },
    methods:{
        getVisitAuthority(){
            let form = new FormData();
            form.append("AID",this.aid);
            form.append("UID",this.uid);
            this.$axios.post("/api/authJudger/",form)
            .then(response=>{
                if(response.data.Status){//
                    this.PrivateOwn=response.data.PrivateOwn//
                    this.TeamOwn=response.data.TeamOwn//
                    this.CanRead=response.data.CanRead//
                    this.CanComment=response.data.CanComment//
                    this.CanEdit=response.data.CanEdit
                    console.log(this.TeamOwn)
                    if(this.CanRead){//
                        console.log("")
                        this.getArticle(this.aid);
                    }
                }
                else{
                    this.$message({
                        message:"",
                        showClose:true,
                        duration:1000,
                        type:"error"
                    })
                }
            })
            .catch(error=>{
                this.$message({
                    message:"1111",
                    showClose:true,
                    duration:1000,
                    type:"error"
                })
            })
        },
        getAllNum(article_id){
            let form = new FormData();
            form.append("AID",article_id);
            this.$axios.post("/api/getcheckNum/",form)
            .then(response=>{
                if(response.data.feedback){//
                    this.haveviews=parseInt(response.data.checknum);
                    this.havemodifys=parseInt(response.data.editnum);
                    this.havecomments=parseInt(response.data.commentnum);
                    console.log(response.data);
                }
                else{
                    this.$message({
                        message:"",
                        showClose:true,
                        duration:1000,
                        type:"error"
                    })
                }
            })
            .catch(error=>{
                this.$message({
                    message:"",
                    showClose:true,
                    duration:1000,
                    type:"error"
                })
            })
        },
        /*/ */
        MD5getAID(){
            let form = new FormData();
            form.append("md5",this.md5);
            this.$axios.post("/api/AIDgetMD5/",form)
            .then(response=>{
                if(response.data.feedback){//
                    this.aid=response.data.AID;
                    if(this.aid!=-1){//
                        console.log("");
                        this.getAllNum(this.aid)
                        this.getVisitAuthority();
                    }
                    else if(this.mid!=-1){
                        console.log("");
                        this.getArticle(this.mid);
                    }
                }
                else{
                    this.$message({
                        message:"",
                        showClose:true,
                        duration:1000,
                        type:"error"
                    })
                }
            })
            .catch(error=>{
                this.$message({
                    message:"",
                    showClose:true,
                    duration:1000,
                    type:"error"
                })
            })
        },
        getArticle(article_id){
            let form = new FormData();
            form.append("AID",article_id);
            form.append("UID",this.uid);
            this.$axios.post("/api/getArticle/",form)
            .then(response=>{
                if(response.data.Status){//
                    this.articleInfo.content=response.data.Content;
                    this.articleInfo.title=response.data.Title;
                }
                else{
                    this.$message({
                        message:"",
                        showClose:true,
                        duration:1000,
                        type:"error"
                    })
                }
            })
            .catch(error=>{
                this.$message({
                    message:"",
                    showClose:true,
                    duration:1000,
                    type:"error"
                })
            })
        },
        /* */
        //
        //
        //
        //
        saveAsMine(type){
            let targetUrl = (type=='doc'?"/api/sendMyArticle/":"/api/sendMyModel/")
            if(type=='model'){//
                this.aid=-1;
            }
            /*let form = new FormData();
            form.append("UID",this.uid);
            form.append("AID",this.aid);
            form.append("Tags",this.Tags);
            if(this.articleInfo.content!="")form.append("Content",this.articleInfo.content);
            else form.append("Content","");
            if(this.articleInfo.title!="")form.append("Title",this.articleInfo.title);
            else form.append("Title","");
            form.append("TID",(this.tid=="-1"?"":this.tid));*/
            let T=[];
            for(var i=0;i<this.Tags.length;i++){
                var obj=new Object();
                obj.name=this.Tags[i].name;
                obj.type=this.Tags[i].type;
                T.push(obj);
            }
            if(this.articleInfo.content=="")
                this.articleInfo.content="";
            if(this.articleInfo.title=="")
                this.articleInfo.title="";
            let form={};
            form={
                TID:(this.tid=="-1"?"":this.tid),
                UID:this.uid,
                AID:this.aid,
                Content:this.articleInfo.content,
                Title:this.articleInfo.title,
                Tags:T
            }
            this.$axios.post(targetUrl,form)
            .then(response=>{
                if(response.data.Status){//
                    this.$message({
                        message:"",
                        duration:1000,
                        showClose:true,
                        type:"success"
                    })
                }
                else{
                    this.$message({
                        message:"",
                        duration:1000,
                        showClose:true,
                        type:"error"
                    })
                }
            })
            .catch(error=>{
                this.$message({
                    message:"",
                    duration:1000,
                    showClose:true,
                    type:"error"
                })
            })
        },
        $imgAdd(pos,$file){
            let form = new FormData();
            form.append('img', $file);
            this.$axios.post("/api/picSave/",form,{
                headers:{
                    'Content-Type':'multipart/form-data'
                }
            }).then(response=>{
                this.$refs.editor.$img2Url(pos,"http://119.45.200.4"+response.data.imgUrl);
            })
        },
    }
}
</script>
<style scoped>
.authTitle{
	width:40%;
	font-weight:bold;
	text-align:left;
	display:inline-block;
	font-size:100%;
	margin:auto auto auto auto;
}
.switches{
    width:55%;
    display:inline-block;
    text-align:right;
    margin:auto auto auto auto;
}
.authOption{
    margin-top:1ex;
}
</style>




