<template>
    <div id="main">
        <!---->
        <el-dialog
            title=""
            :visible.sync="deleteDialogVisible"
            width="30%"
        >
            <span></span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="deleteDialogVisible = false"> </el-button>
                <el-button type="primary" @click="dismiss()"> </el-button>
            </span>
        </el-dialog>
        <!---->
        <el-dialog
            title=""
            :visible.sync="inviteDialogVisible"
            width="30%"
        >
            <p></p>
            <el-input type="text" v-model="inviteeEmail"></el-input>
            <span slot="footer" class="dialog-footer">
                <el-button @click="inviteDialogVisible = false"> </el-button>
                <el-button type="primary" @click="invite()"> </el-button>
            </span>
        </el-dialog>
        <!---->
        <el-dialog
            title=""
            :visible.sync="quitDialogVisible"
            width="30%"
        >
            <span></span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="quitDialogVisible = false"> </el-button>
                <el-button type="primary" @click="quitTeam()"> </el-button>
            </span>
        </el-dialog>
        <!---->
        <div style="border-bottom:1px solid #e8e8e8">
            <h3 style="text-align:left;
                padding-bottom:1ex;
                padding-top:1ex;
                display:inline-block;
                margin:auto auto auto auto;
                width:76%"
            >
                &nbsp;
            </h3>
            <!---->
            <div style="display:inline-block;width:15%">
                <i class="el-icon-plus" style="cursor:pointer" @click="inviteDialogVisible=true" v-if="myLevel>=5"></i>
                <i class="el-icon-delete" style="cursor:pointer" @click="deleteDialogVisible=true" v-if="isFounder()"></i>
                <i class="el-icon-right" style="cursor:pointer" @click="quitDialogVisible=true" v-if="!isFounder()"></i>
            </div>
        </div>
        <!---->
        <div v-for="(member,i) in list" :key="i" style="padding-left:5px;padding-right:5px">
            <!---->
            <el-popover
                placement="bottom"
                trigger="click"
                :ref="'popMenu'+i"
                v-if="!member.Founder&&myLevel>=5&&uid!=member.UID"
            >
                <p @click="kickout(i,member.UID)" class="popoverOption"><i class="el-icon-close"></i>&nbsp;&nbsp;</p>
            </el-popover>
            <!---->
            <el-avatar v-if="member.AvatarUrl" :src="'http://119.45.200.4'+member.AvatarUrl" :fit="'scale-down'" style="vertical-align:middle;display:inline-block" v-popover="'popMenu'+i"></el-avatar>
            <el-avatar v-else
                icon="el-icon-user-solid" 
                style="vertical-align:middle;display:inline-block"
                v-popover="'popMenu'+i"
            ></el-avatar>
            <!---->
            <p style="display:inline-block;width:50%;cursor:pointer" @click="goOtherIndex(member.UID)">{{member.Username}}</p>
            <!---->
            <el-input-number 
                v-model="member.Level" 
                size="mini" 
                style="width:30%" 
                :max="5"
                :min="1"
                @change="submitChanges(i)"
                :disabled="myLevel<5||member.Founder"
            ></el-input-number>
        </div>
    </div>
</template>
<script>
import QS from "qs"
export default {
    name:"memberList",
    props:{
        myLevel:Number,//
        tid:Number//
    },
    data(){
        return{
            deleteDialogVisible:false,//
            inviteDialogVisible:false,//
            quitDialogVisible:false,//
            inviteeEmail:"",//
            list:[],
            uid:0,
        }
    },
    created(){
        /* */
        this.getMemberList();
        this.uid=this.$route.params.UID;
    },
    methods:{
        /* */
        getMemberList(){
            let form = new FormData();
            console.log("TIDUndefined"+this.tid)
            form.append("TID",this.tid);
            this.$axios.post("/api/memberList/",form)
            .then(response=>{
                this.list=response.data.members;
            })
            .catch(error=>{
                this.$message({
                    message:"",
                    showClose:true,
                    duration:1000,
                    type:"error"
                })
            })
        },
        /* */
        submitChanges(i){
            let form = new FormData();
            form.append("TID",this.tid);
            form.append("UID",this.list[i].UID);
            form.append("Level",this.list[i].Level);
            this.$axios.post("/api/modifyRECD/",form)
            .then(response=>{
                if(response.data.Status){//
                    this.$message({
                        message:""+this.list[i].Username+"",
                        type:"success",
                        showClose:true,
                        duration:1000
                    })
                }
                else{
                    this.$message({
                        message:"",
                        type:"error",
                        showClose:true,
                        duration:1000
                    })
                }
            })
            .catch(error=>{
                this.$message({
                    message:"modifyRECD",
                    type:"error",
                    showClose:true,
                    duration:1000
                })
            })
        },
        /* */
        invite(){
            this.inviteDialogVisible=false;
            if(this.inviteeEmail=="")return;
            let form = new FormData();
            form.append("TID",this.tid);
            form.append("TargetEmail",this.inviteeEmail);
            this.$axios.post("/api/inviteMember/",form)
            .then(response=>{
                if(!response.data.Status){//
                    if(response.data.Exist){//
                        this.$message({
                            message:"",
                            duration:1000,
                            showClose:true,
                            type:"error"
                        })
                    }
                    else{//
                        this.$message({
                            message:"",
                            duration:1000,
                            showClose:true,
                            type:"error"
                        })
                    }
                }
                else{
                    this.$message({
                        message:"",
                        duration:1000,
                        showClose:true,
                        type:"success"
                    })
                }
            })
            .catch(error=>{
                this.$message({
                    message:"/api/inviteMember/",
                    duration:1000,
                    showClose:true,
                    type:"error"
                })
            })
        },
        /* */
        dismiss(){
            this.deleteDialogVisible=false;
            let form = new FormData();
            form.append("TID",this.tid);
            this.$axios.post("/api/dismissTeam/",form)
            .then(response=>{
                if(response.data.Status){//
                    this.$message({
                        message:"",
                        type:"success",
                        duration:1000,
                        showClose:true
                    })
                    this.$router.push({
                        path:"/craftTable/"+this.$store.state.userProfile.UID
                    })
                }
                else{
                    this.$message({
                        message:"",
                        type:"error",
                        duration:1000,
                        showClose:true
                    })
                }
            })
            .catch(error=>{
                this.$message({
                    message:"dismissTeam",
                    type:"error",
                    duration:1000,
                    showClose:true
                })
            })
        },
        /* */
        kickout(i,target){
            let form = new FormData();
            form.append("TID",this.tid);
            form.append("UID",target);
            this.list.splice(i,1);
            this.$axios.post("/api/kickMember/",form)
            .then(response=>{
                if(response.data.Status){
                    this.$message({
                        message:"",
                        type:"success",
                        duration:1000,
                        showClose:true
                    })
                }
                else{
                    this.$message({
                        message:"",
                        type:"error",
                        duration:1000,
                        showClose:true
                    })
                }
            })
            .catch(error=>{
                this.$message({
                    message:"kickMember",
                    type:"error",
                    duration:1000,
                    showClose:true
                })
            })
        },
        /* */
        isFounder(){
            for(var i=0; i<this.list.length;i++){
                if(this.$store.state.userProfile.UID==this.list[i].UID&&this.list[i].Founder){
                    return true;
                }
            }
            return false;
        },
        /* */
        quitTeam(){
            let form = new FormData();
            form.append("UID",this.$store.state.userProfile.UID);
            form.append("TID",this.tid);
            this.$axios.post("/api/quitTeam/",form)
            .then(response=>{
                if(response.data.Status){//
                    this.$message({
                        message:"",
                        type:"success",
                        duration:1000,
                        showClose:true
                    })
                    this.$router.replace({
                        path:"/craftTable/"+this.$store.state.userProfile.UID,
                    })
                }
                else{
                    this.$message({
                        message:"",
                        type:"error",
                        duration:1000,
                        showClose:true
                    })
                }
            })
            .catch(error=>{
                this.$message({
                    message:"/api/quitTeam/",
                    type:"error",
                    duration:1000,
                    showClose:true
                })
            })
        },
        goOtherIndex(uid){
            this.$router.push("/OtherIndex/"+uid);
        }
    }
}
</script>
<style scoped>
.popoverOption{
    cursor:pointer;
    padding:1ex 1ex 1ex 1ex;
    margin:auto 0px auto 0px;
}
.popoverOption:hover{
    font-weight: bold;
    background:#e8e8e8;
}
</style>


