<template>
    <div id="main">
        <myHeader :showAvatar="true" :showSearch="false"></myHeader>
        <div style="vertical-align:top">
            <!---->
            <div style="display:inline-block;width:10%;vertical-align:top">
              <LeftSliderPro :team="teams" style="width: 100%;"></LeftSliderPro>
            </div>
            <!---->
            <div style="display:inline-block;width:70%;vertical-align:top">
                <!--div75%mavon-editor-->
                <div style="width:75%;margin-left:2.5%;text-algin:center">
                    <el-tabs
                        v-if="this.$route.params.AID!=-1"
                        v-model="activeName"
                        @tab-click="handleClick"
                        style="width:30%;display:inline-block"
                        :stretch="true"
                    >
                        <el-tab-pane label="" name="editor" style="text-align:center"></el-tab-pane>
                        <el-tab-pane label="" name="comment" style="text-align:center"></el-tab-pane>
                    </el-tabs>
                </div>
                <!---->
                <editor v-show="activeName=='editor'" style="width:100%" :Editable="true" :MyLevel="5" :isTagVisible="true"></editor>
                <comment
                    v-show="activeName=='comment'"
                    :myuid="this.$store.state.userProfile.UID"
                    :AID="this.$route.params.AID"
                    :md5="0"
                    :level="4"
                    :commentlevel="1"
                    style="width:75%;margin-left:2.5%"
                >
                </comment>
            </div>
        </div>
    </div>
</template>
<script>
import leftSider from "@/components/LeftSider/LeftSider.vue"
import editor from "@/components/editor/editor.vue"
import myHeader from "@/components/myHeader/myHeader.vue"
import comment from "@/components/Comment/Comment.vue"
import LeftSliderPro from "../../components/LeftSliderPro/LeftSliderPro.vue";

export default {
    name:"myDocEditor",
    components:{
        leftSider,
        editor,
        myHeader,
        comment,
        LeftSliderPro
    },
    data(){
        return{
            activeName:"editor",
            teams:[]//
        }
    },
    /* */
    /*TODO  */
    /*TODO  */
    created(){
        this.getTeamInfo();
    },
    methods:{
        handleClick(tab, event) {
            console.log(tab, event);
        },

        /* */
        getTeamInfo(){
            let form = new FormData();
            form.append("UID", this.$store.state.userProfile.UID);
            this.$axios.post("/api/teamList/",form)
            .then(response=>{
                if(response.data.Status){//
                    var list = response.data.teams;
                    for(var i in list){
                        console.log(i);
                        this.teams.push({
                            TID:list[i].TID,
                            teamName:list[i].teamName
                        });
                    }
                }
                else{
                    this.$message({
                        message:"",
                        type:"error",
                        duration:1000,
                        showClose:true
                    })
                }
            })
            .catch(error=>{
                this.$message({
                    message:"",
                    type:"error",
                    duration:1000,
                    showClose:true
                })
            })
        }
    }
}
</script>
<style scoped>

</style>



