<template>
    <div style="padding:1ex">
		<h2>{{name}}</h2>
		<hr style="width:100%">
		<p style="text-align:left;padding-left:1ex"><i class="el-icon-info"></i>&nbsp;<br></p>
		<p 
			class="hasDes" 
			v-if="this.describe!=null&&showDes"
			@mouseover="handleHover" 
		>
			{{this.describe}}
		</p>
		<p 
			class="hasntDes" 
			v-if="this.describe==null&&showDes" 
			@mouseover="handleHover"
		>
			{{defaultDes}}
		</p>
		<div v-if="!showDes" @mouseout="submitChange()" style="padding:0px 1ex 1ex 1ex">
			<el-input type="textarea" :autofocus="true" v-model="newDescription" :placeholder="this.describe==null?'':describe"></el-input>
		</div>
    </div> 
</template>
<script>
const clickoutside = {
    update() {},
};
export default {
    props:{
		name: String,
		describe:String,
		tid:Number,//id
		level:Number//
    },
	name:'TeamDescribe',
    data(){
        return{
            form: {
              describe:this.describe,
              name: this.name
			},
			originDes:this.describe,//
			defaultDes:"",
			newDescription:this.describe,//
			showDes:true//
        }
    },
    methods: {
		handleHover(){
			console.log(""+this.level);
			if(this.level>=5)this.showDes=false;
		},
		submitChange(){
			this.showDes=true;
			if(this.newDescription==''||this.newDescription==this.originDes){
				console.log('');
				return;
			}
			let form = new FormData();
			form.append("UID",this.$store.state.userProfile.UID);
			form.append("TID",this.tid);
			form.append("Words",this.newDescription);
			this.$axios.post("/api/changeTeamDescription/",form)
			.then(response=>{
				if(response.data.Status){
					this.$message({
						message:"",
						type:"success",
						duration:1000,
						showClose:true
					});
					this.describe=this.newDescription;
				}
				else{
					this.$message({
						message:"",
						type:"error",
						duration:1000,
						showClose:true
					});
				}
			})
			.catch(error=>{
				this.$message({
					message:"changeTeamDescription",
					type:"error",
					duration:1000,
					showClose:true
				});
			})
		}
    }    
}
</script>
<style scoped>
.hasntDes{
	color:grey;
	text-align:left;
	padding:0px 1ex 1ex 1ex;
}
.hasDes{
	color:black;
	text-align:left;
	padding:0px 1ex 1ex 1ex;
}
</style>



